import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { Dropdown, Menu } from "antd";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "react-redux";
import { CaretDownOutlined, SyncOutlined } from "@ant-design/icons";
import KanbanViewImg from "assets/icons/common/kanbanView.png";
import ListViewImg from "assets/icons/common/listView.png";
import ListViewDetailImg from "assets/icons/common/listViewDetail.png";
import { useNavigate } from "react-router";
import { syncData, updateView } from "redux/slices/enhancementView";
import plusIcon from "assets/icons/objectsManagement/plus.svg";
import { loadListObjectField } from "redux/slices/objects";

const HeaderView = ({ objectId, listParent }) => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { userDetail } = useSelector((state) => state.userReducer);
  // const { objectCategory } = useSelector(
  //   (state) => state.objectsManagementReducer
  // );
  const { listObjectField } = useSelector((state) => state.objectsReducer);

  const [write, setWrite] = useState(false);

  useEffect(() => {
    /* eslint-disable-next-line */
    listObjectField.map((item, idx) => {
      if (Object.keys(item)[0] === "main_object") {
        setWrite(item[Object.keys(item)[0]]?.create_permission);
      }
    });
  }, [listObjectField]);
  useEffect(() => {
    dispatch(
      loadListObjectField({
        api_version: "2",
        object_id: objectId,
        show_meta_fields: true,
      })
    );
  }, [dispatch, objectId]);

  const menuView = (
    <Menu>
      <CustomItem
        onClick={() => {
          dispatch(updateView(true));
          navigate("/objects/" + objectId + "/default-view");
        }}
      >
        <ItemText> List view </ItemText>
        <img src={ListViewImg} alt="Kanban view" />
      </CustomItem>
      <CustomItem
        onClick={() => {
          navigate(`/kanban-view/${objectId}/default-view`);
        }}
      >
        <ItemText> Kanban view </ItemText>
        <img src={KanbanViewImg} alt="Kanban view" />
      </CustomItem>
      <CustomItem
        onClick={() => {
          navigate(`/list-view-with-details/objects/${objectId}/default-view`);
        }}
      >
        <ItemText> List view with detail</ItemText>
        <img src={ListViewDetailImg} alt=" List view with detail" />
      </CustomItem>
    </Menu>
  );
  const renderBreadCrumb = (list) => {
    return list.map((item, index) => (
      <>
        <span>{item}</span>
        {index !== list.length - 1 && (
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="5"
            height="5"
            viewBox="0 0 5 5"
            fill="none"
            style={{ marginTop: "4px" }}
          >
            <circle cx="2.94141" cy="2.91406" r="2" fill="#919EAB" />
          </svg>
        )}
      </>
    ));
  };
  return (
    <CustomWrapperAction>
      <div>
        <NewBreadCrumb
          isHidden={window.location.pathname.includes("/details/")}
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="25"
            height="25"
            viewBox="0 0 25 25"
            fill="none"
            style={{ marginRight: "4px" }}
          >
            <path
              d="M18.7314 23.664H7.15141C4.41141 23.664 2.19141 21.434 2.19141 18.694V11.284C2.19141 9.92404 3.03141 8.21404 4.11141 7.37404L9.50141 3.17404C11.1214 1.91404 13.7114 1.85404 15.3914 3.03404L21.5714 7.36404C22.7614 8.19404 23.6914 9.97404 23.6914 11.424V18.704C23.6914 21.434 21.4714 23.664 18.7314 23.664ZM10.4214 4.35404L5.03141 8.55404C4.32141 9.11404 3.69141 10.384 3.69141 11.284V18.694C3.69141 20.604 5.24141 22.164 7.15141 22.164H18.7314C20.6414 22.164 22.1914 20.614 22.1914 18.704V11.424C22.1914 10.464 21.5014 9.13404 20.7114 8.59404L14.5314 4.26404C13.3914 3.46404 11.5114 3.50404 10.4214 4.35404Z"
              fill="#919EAB"
            />
            <path
              d="M12.9414 19.6641C12.5314 19.6641 12.1914 19.3241 12.1914 18.9141V15.9141C12.1914 15.5041 12.5314 15.1641 12.9414 15.1641C13.3514 15.1641 13.6914 15.5041 13.6914 15.9141V18.9141C13.6914 19.3241 13.3514 19.6641 12.9414 19.6641Z"
              fill="#919EAB"
            />
          </svg>
          {renderBreadCrumb(listParent ? listParent : [])}
        </NewBreadCrumb>
      </div>

      <RightWrap>
        {write && (
          <Button
            style={{ marginRight: "16px" }}
            size="large"
            onClick={() => {
              navigate(
                `/knowledge-base-enhancement/create-article/${objectId}`
              );
            }}
          >
            <img alt="" src={plusIcon} style={{ marginRight: "8px" }} />
            {t("object.addRecord")}
          </Button>
        )}
        {userDetail.Is_Admin && (
          <WrapIcon onClick={() => dispatch(syncData({ object_id: objectId }))}>
            <SyncOutlined />
          </WrapIcon>
        )}

        <ViewWrap>
          <Dropdown overlay={menuView} arrow trigger={["click"]}>
            <OptionView>
              <img src={ListViewImg} alt="" /> <Text>Knowledge</Text>
              <CaretDownOutlined />
            </OptionView>
          </Dropdown>
        </ViewWrap>
      </RightWrap>
    </CustomWrapperAction>
  );
};

export default HeaderView;

const CustomWrapperAction = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  width: 100%;

  .ant-btn:hover {
    border-color: ${(props) => props.theme.main};
  }
  .ant-btn:active {
    color: #fff;
    background-color: ${(props) => props.theme.darker}!important;
    border-color: ${(props) => props.theme.darker}!important;
  }
  .ant-btn:focus {
    color: #fff !important;
    background-color: ${(props) => props.theme.darker}!important;
    border-color: ${(props) => props.theme.darker}!important;
  }
`;

const RightWrap = styled.div`
  display: flex;
`;

const ViewWrap = styled.div`
  width: 140px;
  height: 40px;
  display: none;
  justify-content: center;
  align-items: center;
  padding: 0 8px;
  background: #ffffff;
  border: 1px solid #d9d9d9;
  box-shadow: 0px 2px 0px rgba(0, 0, 0, 0.016);
  border-radius: 2px;
  cursor: pointer;
  margin-left: 16px;
`;

const OptionView = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
`;

const Text = styled.div`
  margin: 0 6px;
  font-size: 16px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
`;

const CustomItem = styled(Menu.Item)`
  .ant-dropdown-menu-title-content {
    display: flex;
    justify-content: space-between;
  }

  img {
    width: 19px;
  }
`;

const ItemText = styled.span`
  font-size: 16px;
  line-height: 22px;
  margin-right: 30px;
  margin-left: 8px;
`;

const Button = styled.div`
  padding: 0 16px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid ${(props) => props.theme.darker};
  cursor: pointer;
  transition: all 0.5s;
  border-radius: 6px;
  font-size: 16px;
  background: ${(props) => props.theme.main};
  color: #fff;

  :hover {
    background: ${(props) => props.theme.darker} !important;
    color: #fff;
  }
`;

const WrapIcon = styled.div`
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 6px;
  border: 1px solid #d9d9d9;
  cursor: pointer;
  background: #fff;
  svg {
    font-size: 16px;
  }
`;

const NewBreadCrumb = styled.div`
  flex: 1;
  display: ${(props) => (props.isHidden ? "flex" : "none")};
  align-items: center;
  span {
    margin: 0px 8px;
    margin-top: 4px;
    font-size: 16px;
    font-family: var(--roboto-500);
    color: #637381;
    :last-child {
      color: #212b36;
    }
  }
`;
