import React from "react";
import styled from "styled-components";
import { EyeFilled, LikeFilled, CalendarFilled } from "@ant-design/icons";
import _ from "lodash";
import { FE_URL } from "constants/constants";
import { useParams } from "react-router";
import CopyImg from "assets/icons/knowledgeEnhancement/LinkIcon.svg";
import { Tooltip } from "antd";
import { Notification } from "components/Notification/Noti";
import { useTranslation } from "react-i18next";
import parse from "html-react-parser";
import { optionsParse } from "util/staticData";
import AuthenImage from "components/Image/AuthenImage";

const TopView = ({ setting, listArticleTopview, isTop, isDetail, config }) => {
  const { objectId } = useParams();
  const { t } = useTranslation();
  const renderSrc = (setting, article) => {
    if (_.get(article, `${setting?.fld_thumbnail}.value`, [])) {
      return _.get(article, `${setting?.fld_thumbnail}.value`, [])[0];
    } else {
      return "";
    }
  };
  const renderContent = (list, setting) => {
    return list.map((article, index) => {
      return (
        <TopArticle
          onClick={() => {
            window.open(
              `${FE_URL}/knowledge-base-enhancement/details/${objectId}/${article._id}`
            );
          }}
        >
          <CustomWrap>
            {renderSrc(setting, article) && (
              <AuthenImage src={renderSrc(setting, article)} />
            )}
            <ArticleWrap>
              <TitleTop>
                <span>
                  {_.get(article, `${setting?.fld_title}.value`, "No title")}
                </span>
                <Tooltip title={t("knowledgeBase.copy")}>
                  <img
                    src={CopyImg}
                    alt="Copy"
                    onClick={(e) => {
                      e.preventDefault();
                      e.stopPropagation();
                      navigator.clipboard.writeText(
                        `${FE_URL}/knowledge-base-enhancement/details/${objectId}/${article._id}`
                      );
                      Notification("success", "Copy link article success");
                    }}
                  />
                </Tooltip>
              </TitleTop>
              {_.get(article, `${setting?.fld_body}.value`, "No body") && (
                <Content>
                  {parse(
                    _.get(
                      article,
                      `${setting?.fld_body}.value`,
                      "No body"
                    ).replace(/<[^>]*>/g, ""),
                    optionsParse
                  )}
                </Content>
              )}
            </ArticleWrap>
          </CustomWrap>
          <ActionWrap>
            {config.show_votes && (
              <Action>
                <LikeFilled />
                {_.get(article, `${setting?.fld_upvotes}.value`, "0")
                  ? _.get(article, `${setting?.fld_upvotes}.value`, "0")
                  : "0"}
              </Action>
            )}
            {config.show_total_views && (
              <Action>
                <EyeFilled />
                {_.get(article, `${setting?.fld_total_views}.value`, "0")
                  ? _.get(article, `${setting?.fld_total_views}.value`, "0")
                  : "0"}
              </Action>
            )}
            <Action>
              <CalendarFilled /> {article.created_date}
            </Action>
          </ActionWrap>
        </TopArticle>
      );
    });
  };
  return (
    <Wrap isDetail={isDetail}>
      <Header>
        {isTop ? t("knowledgeBase.topView") : t("knowledgeBase.newestArticle")}
      </Header>
      {renderContent(listArticleTopview, setting)}
    </Wrap>
  );
};

export default TopView;

const Wrap = styled.div`
  background: #fff;
  width: 100%;
  line-height: normal;
  color: #282828;
  border-radius: 6px;
  border: 1px solid #ececec;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.05);
  padding: 24px;
  margin-bottom: 16px;
`;

const TopArticle = styled.div`
  margin-top: 24px;
  :first-child {
    margin-top: 24px;
  }
  cursor: pointer;
  img {
    width: 108px;
    height: 108px;
    border-radius: 8px;
    object-fit: cover;
    margin-right: 20px;
  }
`;

const Header = styled.div`
  font-size: 20px;
  font-family: var(--roboto-500);
  line-height: normal;
  width: fit-content;
  border-bottom: 1px solid ${(props) => props.theme.main};
  padding-bottom: 8px;
`;

const ArticleWrap = styled.div`
  flex: 1;
  cursor: pointer;
`;

const Content = styled.div`
  color: #6b6b6b;
  font-size: 16px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  word-break: break-word;
  p {
    margin-bottom: 0px;
  }
`;
const TitleTop = styled.div`
  font-size: 16px;
  font-family: var(--roboto-500);
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
  span {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    line-height: normal;
  }

  img {
    margin-left: 6px;
    margin-right: 0px;
    width: 20px;
    height: 20px;
  }
`;
const ActionWrap = styled.div`
  display: flex;
  padding-bottom: 8px;
  color: #919eab;
  svg {
    color: #919eab;
  }
`;
const Action = styled.div`
  margin-right: 16px;
  font-size: 12px;
  line-height: 16px;
  font-family: var(--roboto-500);
  display: flex;
  margin-top: 8px;
  svg {
    font-size: 16px;
    margin-right: 4px;
    cursor: pointer;
  }
`;
const CustomWrap = styled.div`
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin-bottom: 8px;
`;
